import React from 'react';
import './ImageLinkForm.css';

const ImageLinkForm = ({onInputChange, onButtonClick}) => {
    return(
        <div>
            <p className='f3' style={{textAlign: 'center'}}>
                {`C'mon put the link already FacePalm and let the magic happen Kappa`}
            </p>
            <div className='center'>
                <div className='form center pa4 br3 shadow-5'>
                    <input className='f4 pa2 w-70 center' onChange={onInputChange} type='text'/>
                    <button className='w-30 grow f4 link ph3 pv2 dib white bg-light-purple' onClick={onButtonClick}>Detect</button>
                </div>
            </div>
        </div>
    )
}

export default ImageLinkForm;