import React from 'react';

const Navigation = ({ onRouteChange, isSignedIn }) => {
    if(isSignedIn){
        return(
            <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
                <p className="f3 pointer pa3 underline link dim black" onClick={() => onRouteChange('signout')}>Sign Out</p>
            </nav>)
    }
    else {
        return(
            <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
                <p className="f3 pointer pa3 underline link dim black" onClick={() => onRouteChange('signin')}>Sign In</p>
                <p className="f3 pointer pa3 underline link dim black" onClick={() => onRouteChange('register')}>Register</p>
            </nav>
        )
    }   
}

export default Navigation;